import React, { useEffect, useState } from 'react';
import axios from 'axios';

function BusinessRegistrationCheck() {
    const [licenseNumber, setLicenseNumber] = useState('');
    const [registrationStatus, setRegistrationStatus] = useState('');

    useEffect(() => {
        businessNumber(); // 컴포넌트가 마운트될 때 자동으로 실행
    }, []);

    const businessNumber = async () => {
        try {
            const response = await axios.post('인증키주소', {
                b_no: [licenseNumber]
            });

            const valid = response.data.data[0]['b_stt_cd'];
            setRegistrationStatus(valid === '01' ? '사업자 회원가입이 가능합니다.' : '사업자 회원가입을 할 수 없습니다.');
        } catch (error) {
            console.error('에러가 발생했습니다.', error);
        }
    };

    const handleCheckRegistration = () => {
        businessNumber();
    };

    return (
        <div>
            <label htmlFor="license">사업자번호:</label>
            <input
                type="text"
                id="license"
                value={licenseNumber}
                onChange={(e) => setLicenseNumber(e.target.value)}
            />
            <button onClick={handleCheckRegistration}>등록 확인</button>
            <p>{registrationStatus}</p>
        </div>
    );
}

export default BusinessRegistrationCheck;
